import React from "react";

export default function Hero(props) {
  const styles = {
    display: props.heroImage ? "block" : "none",
  };
  return (
    <>
      {props.title && (
        <div style={styles} className="hero">
          <h1>{props.title}</h1>
          {props.title && <img src={props.heroImage}></img>}
          <div className="info">
            <p>
              <strong>{props.tagline}</strong>
            </p>
            <p className="overview">{props.overview}</p>
            <p className="budget">Budget: ${props.budget}</p>
            <a target="_blank" href={props.homepage}>
              Watch here!
            </a>
          </div>
        </div>
      )}
    </>
  );
}
