import "./App.css";
import {
  SEARCH_BASE_URL,
  POPULAR_BASE_URL,
  API_URL,
  API_KEY,
  IMAGE_BASE_URL,
  BACKDROP_SIZE,
  POSTER_SIZE,
  REQUEST_TOKEN_URL,
  LOGIN_URL,
  SESSION_ID_URL,
} from "./config.js";

//componenets
import Header from "./components/Header.js";
import Box from "./components/Box";
import Hero from "./components/Hero";

import { useEffect, useState } from "react";

function App() {
  const [boxes, setBoxes] = useState([]);
  const [input, setInput] = useState("");
  const [movie, setMovie] = useState({});
  const get = async (search) => {
    try {
      const res = await fetch(
        search ? `${SEARCH_BASE_URL}${search}` : `${POPULAR_BASE_URL}`
      );
      if (!res.ok) throw new Error("Something Went Wrong");
      const data = await await res.json();

      console.log(data.results);

      setBoxes(data.results);
    } catch (err) {
      return err.message;
    }
  };

  useEffect(() => {
    const search = "";
    get(search);
  }, []);

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [movie]);

  const getMovie = async (movieId) => {
    const endpoint = `${API_URL}movie/${movieId}?api_key=${API_KEY}`;
    const movieData = await (await fetch(endpoint)).json();

    console.log(movieData);

    setMovie(movieData);
  };

  function handleClick(id) {
    getMovie(id);
  }

  const boxElements = boxes.map((movie) => (
    <Box
      key={movie.id}
      id={movie.id}
      image={IMAGE_BASE_URL + POSTER_SIZE + movie.poster_path}
      movieName={movie.original_title}
      handleClick={handleClick}
    />
  ));

  //handling change

  function handleChange(event) {
    setMovie({});
    setInput((prevValue) => {
      get(event.target.value);
      return event.target.value;
    });
  }

  return (
    <div className="App">
      <Header value={input} handleChange={handleChange} />
      {boxes.length != 0 ? (
        <>
          <Hero
            title={movie.original_title}
            tagline={movie.tagline}
            budget={movie.budget}
            homepage={movie.homepage}
            heroImage={`${IMAGE_BASE_URL}${POSTER_SIZE}${movie.poster_path}`}
            overview={movie.overview}
          />
          <div className="grid">{boxElements}</div>
        </>
      ) : (
        <h1 className="empty">Wow Such Empty...(</h1>
      )}
    </div>
  );
}

export default App;

